.photoDisplay-wrapper{
    margin: 40px auto;
    @apply text-[#ffffff] w-[60%] h-[50%]
}
.photoDisplay-div{
    @apply flex flex-col flex-wrap border-2 items-center p-3 border-[#973434] rounded-lg w-[100%] h-[100%]
}
.photoDisplay-photo_div{
}
.photoDisplay-btn_div{
    @apply flex justify-between items-center px-[1%]
}
.photoDisplay-btn{
    @apply bg-[#973434] py-2 px-10 rounded-md text-[13px] font-semibold my-3
}